import React from "react";
import { NavLink } from 'react-router-dom';
const Common = (props) => {

    return (
        <>
        <section className="d-flex align-items-center" >
            <div className="container-fluid">
                <div className="row">
                    <div className="col-10 mx-auto">
                    <div className="row">
                        <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                            <h1>
                                {props.name} <strong className="text-primary font-italic"> - Sougata Sikdar</strong></h1>
                                <h2>We are the team of Talented Developer making Websites</h2>
                                <div className="mt-3">
                                <NavLink  to= {props.visit} className="btn btn-success" > {props.btname}</NavLink>
                                </div>
                                </div>
                                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1">
                            <img src= {props.imgsrc} className="img-fluid p-5 img_animate" alt="firstimg" />
                            </div>
                                
                                </div></div></div></div>
        </section>
        </>
    );
};
export default Common;